body {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  background-color: #d8f3dc;
}

.heading {
  text-align: center;
  font-size: 3rem;
  font-weight: bolder;
  background-color: #5274b7;
  color: white;
  padding: 1rem 0;
}

.lbs {
  font-size: 1.5rem;
  font-family: sans-serif;
}

.inp {
  color: #8707ff;
  border: 2px solid #8707ff;
  border-radius: 10px;
  padding: 10px 25px;
  background: transparent;
  max-width: 190px;
}

.inp:active {
  box-shadow: 2px 2px 15px #8707ff inset;
}

.square {
  background: #cafff4;
  color: #001d3d;
  border: 1px solid #999;
  float: left;
  width: 5.5rem;
  height: 5.5rem;
  font-size: 5rem;
  font-weight: bold;
  line-height: 5rem;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0.5rem;
  text-align: center;
}

.bparent {
  display: flex;
  justify-content: center;
  align-items: center;
}

.inps {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 0.5rem;
  padding: 0.5rem;
}

.btn {
  padding: 10px 20px;
  text-transform: uppercase;
  border-radius: 8px;
  font-size: 17px;
  font-weight: 500;
  color: #ffffff80;
  background-color: #4a4e69;
  text-shadow: none;
  border: 1px solid #ffffff80;
  transition: 0.5s ease;
  user-select: none;
}

.btn:hover {
  color: #ffffff;
  background: #008cff;
  border: 1px solid #008cff;
  text-shadow: 0 0 5px #ffffff,
               0 0 10px #ffffff,
               0 0 20px #ffffff;
  box-shadow: 0 0 5px #008cff,
              0 0 20px #008cff,
              0 0 50px #008cff,
              0 0 100px #008cff;
}

.win {
  font-size: 3rem;
  font-weight: bolder;
  text-align: center;
}

.play {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.1rem;
  color: #e71d36;
}
